import React,{Component} from "react"
import { Button,DatePicker,Input,Table,Select } from 'antd';
import { Link } from 'react-router';
import "../../less/usrmgmt.less"
import axios from "axios";
const Option = Select.Option;

let token=localStorage.getItem("robertUserName");
export default class UsrMgmt extends Component{
    constructor(props){
        super(props);



      this.state={
        applyPage:{
          pageSize:10,
          consultationName:"",
          username:"",
          phone:"",
          status:"1",
          startTime:""
        },
        total:10,
        current:1,
        columns : [
          {
          title:'',
          dataIndex: 'id',
          key: 'id',
          render: (text, record, index) => {
             return <span>{index + 1}</span>
          }
          },
          {
          title: '',
          dataIndex: 'title',
          key: 'title',
          },
          {
          title: '',
          dataIndex: 'startTime',
          key: 'startTime',
          },
          {
          title: '',
          dataIndex: 'username',
          key: 'username',
          },
          {
            title: '',
            dataIndex: 'crea1tAt',
            key: 'creat1At',
          },
          {
            title: '',
            key: 'action',
            render: (text, record,index) => (
              <span  key={record.id}>
              <Link to={"apply/editCnsulation/"+record.id}></Link>
              <Link to=""  className="apply_link"></Link>

              <Link to="" className="apply_link" ></Link>

            </span>
            )
          }
        ],
        dataSource : []
      }
    }

    componentDidMount(){
     // this.query(1)
    }

    push(id,index){

      let that=this;
      axios.request({
        url: '/api/conference/commit',
        method: 'get',
        params:{
          id:id
        },
        headers: {
          'Authorization': 'Bearer '+token,
          'Content-Type': 'application/x-www-form-urlencoded UTF-8'
        },
      }).then(function(response) {
          that.query();

      }).catch(function () {
        alert("!")
      });

    }

  changePage(page){
    this.query(page);
    this.setState({
      current:page
    })
  }

    changeConsultationName(e){
      let apply=this.state.applyPage;
      apply.consultationName=e.target.value;
      this.setState({
        applyPage:apply
      })
    }

    changeUsername(e){
      let apply=this.state.applyPage;
      apply.username=e.target.value;
      this.setState({
        applyPage:apply
      })
    }

    changePhone(e){
      let apply=this.state.applyPage;
      apply.phone=e.target.value;
      this.setState({
        applyPage:apply
      })
    }

    query(num){
      let that=this;
      let applyPage=this.state.applyPage;
      applyPage.pageNum=num;
      axios.request({
        url: '/api/conference/applyPageList',
        method: 'get',
        params:applyPage,
        headers: {
          'Authorization': 'Bearer '+token,
          'Content-Type': 'application/x-www-form-urlencoded UTF-8'
        },
      }).then(function(response) {
        let dataSource=response.data.result?response.data.result.data:[];
        that.setState({
          dataSource:dataSource,
          total:response.data.result.count
        })
      });
    }
    render(){
      return (
        <div>
          <div className="apple_top">
          <h1>
            
            <Button type="primary" onClick={()=>this.query()} className="search_btn1"></Button>
          </h1>
          <ul className="search_ul">
            <li>
              <span className="most_flex"></span>
              <Input onChange={this.changeConsultationName.bind(this)}  className="search_input" size="large" placeholder="" />
            </li>
            <li>
              <span className="most_flex"></span>
              <Input onChange={this.changeConsultationName.bind(this)}  className="search_input" size="large" placeholder="" />
            </li>
            <li>
              <span className="most_flex"></span>
              <Input readOnly onChange={this.changeUsername.bind(this)}  className="search_input" size="large" placeholder="" />
            </li>
            {/*<li>
              <span className="most_flex"></span>
              <Select defaultValue="" className="search_input">
                <Option value="1"></Option>
                <Option value="0"></Option>
              </Select>
            </li>*/}
            <li>
              <span className="most_flex"></span>
              <Input onChange={this.changeConsultationName.bind(this)}  className="search_input" size="large" placeholder="" />
            </li>
          </ul>


            <ul className="search_ul">
              {/*<li>
                <span className="most_flex"></span>
                <Select defaultValue="" className="search_input">
                  <Option value="0">--</Option>
                  <Option value="1"></Option>
                  <Option value="2"></Option>
                </Select>
              </li>*/}

              <li>
              </li>
              <li>
              </li>
            </ul>

          </div>
          <div className="apple_bottom">
            <h1 className="most_h1">
              
              <Link to="usrmgmt/addUsrmgmt">
                <Button type="primary"  className="search_btn2"></Button>
                {/*<Button type="primary"  className="search_btn2"></Button>
                <Button type="primary"  className="search_btn2"></Button>*/}

              </Link>

            </h1>

            <Table pagination={{defaultPageSize:10,showQuickJumper:true,onChange:this.changePage.bind(this),
              total:this.state.total,current:this.state.current }}  rowKey="id" dataSource={this.state.dataSource} columns={this.state.columns} />
          </div>
        </div>
      )
    }

}



